'use client'

import { useState } from 'react'
import { useParams } from 'next/navigation'
import Navigation from '../../../components/Navigation'
import ArtworkModal from '../../../components/ArtworkModal'
import { artworks, seriesOrder } from '../../../content/artworks'

export default function SeriesPage() {
  const { slug } = useParams()
  const series = seriesOrder.find(s => s.slug === slug)
  const [selectedArtwork, setSelectedArtwork] = useState<(typeof artworks)[number] | null>(null)

  if (!series) return <div>Series not found</div>

  const seriesWorks = artworks.filter(w => w.series === series.name)

  return (
    <div style={{ backgroundColor: '#ffffff', minHeight: '100vh', color: '#000' }}>
      <div className="grain-overlay" style={{ opacity: 0.02 }} />
      <Navigation />
      
      <ArtworkModal
        isOpen={!!selectedArtwork}
        onClose={() => setSelectedArtwork(null)}
        artwork={
          selectedArtwork || { id: '', title: '', year: 0, medium: '', size: '', image: '' }
        }
      />

      <div style={{ maxWidth: '1400px', margin: '0 auto', padding: '180px 60px 120px' }}>
        <h2 style={{ 
          fontSize: '12px', 
          letterSpacing: '0.4em', 
          color: '#000', 
          marginBottom: '80px', 
          textTransform: 'uppercase', 
          fontWeight: 600,
          textAlign: 'center'
        }}>
          {series.name}
        </h2>

        <div style={{ 
          display: 'grid', 
          gridTemplateColumns: 'repeat(auto-fill, minmax(400px, 1fr))', 
          gap: '100px 60px' 
        }}>
          {seriesWorks.map((artwork) => (
            <div
              key={artwork.id}
              onClick={() => setSelectedArtwork(artwork)}
              style={{ cursor: 'pointer', display: 'flex', flexDirection: 'column', alignItems: 'center' }}
            >
              <div style={{ 
                width: '100%',
                aspectRatio: '4/5', 
                backgroundColor: '#fdfdfd', 
                marginBottom: '40px', 
                overflow: 'hidden',
                boxShadow: '0 20px 40px -10px rgba(0,0,0,0.05)'
              }}>
                <img
                  src={artwork.image}
                  alt={artwork.title}
                  style={{ 
                    width: '100%', 
                    height: '100%', 
                    objectFit: 'cover',
                    imageRendering: '-webkit-optimize-contrast'
                  }}
                />
              </div>

              <div style={{ textAlign: 'center', maxWidth: '80%' }}>
                <p style={{ 
                  fontFamily: '"Times New Roman", Times, serif',
                  fontSize: '20px', 
                  letterSpacing: '-0.01em', 
                  textTransform: 'none', 
                  fontWeight: 'bold', 
                  margin: '0 0 10px 0' 
                }}>
                  {artwork.title}
                </p>
                <p style={{ 
                  fontSize: '12px', 
                  letterSpacing: '0.1em', 
                  color: '#999', 
                  textTransform: 'uppercase', 
                  fontWeight: 400, 
                  margin: 0 
                }}>
                  {artwork.year}
                </p>
              </div>
            </div>
          ))}
        </div>
      </div>
    </div>
  )
}
