'use client'

import { useState, useRef } from 'react'

interface ArtworkModalProps {
  isOpen: boolean
  onClose: () => void
  artwork: {
    id: string
    title: string
    year: number
    medium: string
    size: string
    image: string
  }
}

export default function ArtworkModal({ isOpen, onClose, artwork }: ArtworkModalProps) {
  const [zoom, setZoom] = useState(1)
  const [pan, setPan] = useState({ x: 0, y: 0 })
  const [isPanning, setIsPanning] = useState(false)
  const [panStart, setPanStart] = useState({ x: 0, y: 0 })
  const containerRef = useRef<HTMLDivElement>(null)

  if (!isOpen) return null

  const handleWheel = (e: React.WheelEvent) => {
    e.preventDefault()
    const zoomSpeed = 0.1
    const newZoom = e.deltaY > 0 ? zoom - zoomSpeed : zoom + zoomSpeed
    setZoom(Math.max(1, Math.min(newZoom, 4)))
  }

  const handleMouseDown = (e: React.MouseEvent) => {
    setIsPanning(true)
    setPanStart({ x: e.clientX - pan.x, y: e.clientY - pan.y })
  }

  const handleMouseMove = (e: React.MouseEvent) => {
    if (!isPanning) return
    setPan({
      x: e.clientX - panStart.x,
      y: e.clientY - panStart.y,
    })
  }

  const handleMouseUp = () => {
    setIsPanning(false)
  }

  return (
    <div 
      style={{ 
        position: 'fixed', 
        inset: 0, 
        backgroundColor: '#ffffff', 
        zIndex: 200, 
        display: 'flex', 
        overflow: 'hidden' 
      }} 
      onClick={onClose}
    >
      {/* Texture Grain Overlay */}
      <div className="grain-overlay" style={{ opacity: 0.03 }} />

      {/* Close button */}
      <button
        onClick={onClose}
        style={{
          position: 'absolute',
          top: '40px',
          right: '40px',
          backgroundColor: 'transparent',
          border: 'none',
          color: '#000',
          fontSize: '10px',
          letterSpacing: '0.3em',
          cursor: 'pointer',
          zIndex: 210,
          textTransform: 'uppercase',
          fontWeight: 600
        }}
      >
        Close [esc]
      </button>

      {/* Main Image Viewport */}
      <div 
        style={{ 
          flex: 1, 
          position: 'relative', 
          overflow: 'hidden', 
          height: '100%', 
          display: 'flex', 
          alignItems: 'center', 
          justifyContent: 'center',
          cursor: zoom > 1 ? (isPanning ? 'grabbing' : 'grab') : 'zoom-in'
        }} 
        onClick={(e) => e.stopPropagation()}
      >
        <div
          ref={containerRef}
          style={{ width: '100%', height: '100%', display: 'flex', alignItems: 'center', justifyContent: 'center' }}
          onWheel={handleWheel}
          onMouseDown={handleMouseDown}
          onMouseMove={handleMouseMove}
          onMouseUp={handleMouseUp}
          onMouseLeave={handleMouseUp}
        >
          <img
            src={artwork.image}
            alt={artwork.title}
            style={{
              maxWidth: '85%',
              maxHeight: '85%',
              objectFit: 'contain',
              userSelect: 'none',
              transform: `scale(${zoom}) translate(${pan.x / zoom}px, ${pan.y / zoom}px)`,
              transition: isPanning ? 'none' : 'transform 0.3s ease-out',
              imageRendering: '-webkit-optimize-contrast'
            }}
          />
        </div>
      </div>

      {/* Metadata Side Column */}
      <div 
        style={{ 
          width: '400px', 
          height: '100%', 
          borderLeft: '1px solid #f0f0f0', 
          backgroundColor: '#ffffff', 
          display: 'flex', 
          flexDirection: 'column', 
          justifyContent: 'center', 
          padding: '60px', 
          color: '#000' 
        }}
        onClick={(e) => e.stopPropagation()}
      >
        <div style={{ display: 'flex', flexDirection: 'column', gap: '60px' }}>
          <div>
            <p style={{ fontSize: '9px', letterSpacing: '0.3em', color: '#999', textTransform: 'uppercase', marginBottom: '10px' }}>Title</p>
            <p style={{ fontFamily: '"Times New Roman", Times, serif', fontSize: '24px', fontWeight: 'bold', fontStyle: 'italic', margin: 0 }}>{artwork.title}</p>
          </div>

          <div>
            <p style={{ fontSize: '9px', letterSpacing: '0.3em', color: '#999', textTransform: 'uppercase', marginBottom: '10px' }}>Year</p>
            <p style={{ fontSize: '14px', letterSpacing: '0.1em', margin: 0 }}>{artwork.year}</p>
          </div>

          <div>
            <p style={{ fontSize: '9px', letterSpacing: '0.3em', color: '#999', textTransform: 'uppercase', marginBottom: '10px' }}>Medium</p>
            <p style={{ fontSize: '14px', letterSpacing: '0.05em', lineHeight: '1.6', margin: 0 }}>{artwork.medium}</p>
          </div>

          <div>
            <p style={{ fontSize: '9px', letterSpacing: '0.3em', color: '#999', textTransform: 'uppercase', marginBottom: '10px' }}>Dimensions</p>
            <p style={{ fontSize: '14px', letterSpacing: '0.05em', margin: 0 }}>{artwork.size}</p>
          </div>

          <div style={{ paddingTop: '40px', borderTop: '1px solid #f0f0f0' }}>
            <button style={{ 
              backgroundColor: 'transparent', 
              border: 'none', 
              color: '#000', 
              fontSize: '10px', 
              letterSpacing: '0.4em', 
              textTransform: 'uppercase', 
              fontWeight: 600,
              cursor: 'pointer',
              padding: 0
            }}>
              Inquire
            </button>
          </div>
        </div>
      </div>
    </div>
  )
}
